import { isAuthorized } from "hooks/useAuthorized";
import { Route, Redirect } from "react-router-dom";

export const AuthorizedRoute = ({
  component: Component,
  allowedRoles,
  ...rest
}) => {
  const userRole = localStorage.getItem("designation");

  return (
    <Route
      {...rest}
      render={(props) => {
        if (isAuthorized(userRole, allowedRoles)) {
          return <Component {...props} />;
        } else {
          return <Redirect to="/404" />; // Redirect to an unauthorized page
        }
      }}
    />
  );
};
